<!--  -->
<template>
  <div class="live-play">
    <el-scrollbar style="height:100%">
      <header>
        <div class="logo">
          <img src="./../../../assets/image/sync-logo.png" alt />
          <h3></h3>
        </div>
        <div class="main">
          <img src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png" alt />
        </div>
      </header>
      <div class="video">
        <div class="video-wrap">
          <div class="video-main">
            <video-player  
              class="video-player-box"
              ref="videoPlayer"
              :playsinline="true"
              :options="playerOptions"
              >
            </video-player>
          </div>
          <div class="video-info">
            <div class="title">{{livingInfo.liveRoomName}}</div>
            <div class="auth flex-between">
              <span>{{livingInfo.userName}}</span>
              <span>{{livingInfo.orgName}}</span>
              <span>{{livingInfo.lookingNumbers || 0}}</span>
            </div>
            <div class="desc">
              <el-scrollbar style="height:100%">
                <div class="content">{{livingInfo.remark}}</div>
              </el-scrollbar>
            </div>
            <div class="control">
              <div @click="handleCopy" class="copy">
                <i class="iconfont icon-mix"></i> 
              </div>
              <div class="share">
                <i class="iconfont icon-share"></i> 
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="commend">
        <div class="content">
          <div class="commend-box">
            <el-input type="textarea" :maxlength="200" v-model="commentValue"></el-input>
            <div class="pub-btn">
              <span>{{commentValue.length}}/200</span>
              <span @click="handleAddComment"></span>
            </div>
          </div>
          <div class="commend-list">
            <div class="title"></div>
            <div class="comment-box" v-for="item in comments" :key="item.live_comment_id">
              <div class="avatar">
                <img :src="item.userFace" alt />
              </div>
              <div class="info">
                <div class="name">
                  <span>{{item.userName}}</span>
                </div>
                <p class="comment-content">
                  {{item.commentContext}}
                </p>
                <div class="time">{{item.createtime|dateFormat}}</div>
              </div>
            </div>
          </div>    
          <!-- <div class="more">
            <span></span>
          </div>      -->
        </div>

      </div>
    </el-scrollbar>
  </div>
</template>

<script>
import {dateFormat} from './../../../assets/javascript/ultis'
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'
import 'videojs-flash'
import SWF from 'videojs-swf/dist/video-js.swf'
export default {
  props: ["id","code"],
  components: {
    videoPlayer
  },
  data() {
    return {
      commentValue: "",
      livingInfo: {},
      comments:[],
      playerOptions: {
        live: true,
        height: '468',
        width:'880',
        sources: [{
          type: "rtmp/mp4",
          src:''
        }],
        techOrder: ['flash'],
        autoplay: true,
        controls: true,
        flash: { swf:SWF }
      },
      randomNumber:Math.ceil(Math.random()*100) + new Date().getTime()
    };
  },
  created() {
    this.code && this.addNumber();
    this.id && this.initLivingData();
    this.id && this.getLivingComment();
  },
  mounted(){
    this.getCommentSetTimeOut();
    window.addEventListener("beforeunload",e => {
      this.reduceNumber()
    })
  },
  filters:{
    dateFormat(val) {
      return dateFormat(new Date(val),"yyyy-MM-dd hh:mm:ss")
    }
  },
  beforeDestroy(){
    clearInterval(this.timer);
    this.reduceNumber();
  },
  destroyed(){
    window.removeEventListener('beforeunload',e => {
      this.reduceNumber()
    })
  },
  methods: {
    initLivingData() {
      const params = { liveRoomId: this.id };
      this.$api.getLiveroomDetail(params).then(res => {
        this.livingInfo = res.data;
        this.$refs.videoPlayer.player.src(res.data.rtmpUrl)
      });
    },
    getLivingComment() {
      const params = { liveRoomId: this.id };
      this.$api.getLiveComment(params).then(res => {
        this.comments = res.data;
      });
    },
    getCommentSetTimeOut(){
      clearInterval(this.timer)
      this.timer = setInterval(()=>{
        this.getLivingComment();
      },1000)
    },
    // linlkpage(){

    // },
    // linlkpageSetTimeOut(){

    // },
    // 
    handleAddComment(){
      let str = this.commentValue.replace(/^\s*|\s*$/g,"");
      if(!str) {
        return this.$myLayer.dangerLayer("")
      }
      const params = {
        commentContext:this.commentValue,
        liveRoomId:this.id
      }
      this.$api.livecommentInsert(params).then(res => {
        this.commentValue = ""
      })
    },

    /* addNumber */
    addNumber(){
      const params = {
        code: this.code,
        number:this.randomNumber
      }
      this.$api.addNumber(params)
    },
    /* reduceNumber */
    reduceNumber(){
      const params = {
        code: this.livingInfo.rtmpCode,
        number:this.randomNumber
      }
      this.$api.reduceNumber(params)
    },
    /* copy */
    handleCopy(){
      // 
    }
  }
};
</script>

<style lang="scss" scoped>
.live-play {
  height: 100%;
}
.live-play header {
  height: 72px;
  background: rgba(255, 255, 255, 1);
  display: flex;
  box-shadow: 0px 5px 8px 0px rgba(23, 38, 71, 0.1);
  .logo {
    width: 200px;
    height: 100%;
    img {
      width: 24px;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    h3 {
      color: #4c84ff;
      font-size: 20px;
      margin-left: 6px;
    }
  }
  .main {
    height: 100%;
    flex: 1;
    padding: 0 42px 0 32px;
    line-height: 72px;
    text-align: right;
    img {
      width: 26px;
      border-radius: 50%;
    }
  }
}
.live-play .video {
  background: #fff;
  margin-top: 30px;
}
.video .video-wrap {
  width: 1200px;
  margin: 0 auto;
  height: 468px;
  display: flex;
}
.video-wrap .video-main {
  flex: 1;
  video {
    width: 100%;
    height: 100%;
    outline: none;
  }
}
.video-wrap .video-info {
  width: 320px;
  background: #141414;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .title {
    font-size: 16px;
    color: #909399;
    padding: 16px 32px 0 16px;
  }
  .auth {
    border-top: 1px solid rgba(144, 147, 153, 0.5);
    span {
      color: #909399;
    }
    margin: 10px 32px 0 16px;
    padding-top: 16px;
  }
  .desc {
    height: 258px;
    color: #fff;
    padding: 16px 0 0 16px;
    .content {
      text-align: justify;
      padding-right: 20px;
      color: #909399;
    }
  }
  .control {
    display: flex;
    align-items: center;
    justify-content: center;
    padding:0 32px 30px 16px;
    > div {
      cursor: pointer;
      color: #909399;
      i {
        color: inherit;
      }
    }
    .copy {
      margin-right: 30px;
    }
  }
}

.commend {
  overflow: hidden;
  min-height: calc(100% - 570px);
  background: #f1f3f8;
}
.commend .content {
  width: 1200px;
  margin: 24px auto;
  margin-bottom: 64px;
  background: #fff;
  // 
}

.content .commend-box {
  padding: 30px 30px 0;
  .pub-btn {
    text-align: right;
    margin-top: 16px;
    margin-bottom: 24px;
  }
  .pub-btn > span:nth-child(1) {
    color: #cacbcc;
  }
  .pub-btn > span:nth-child(2) {
    width: 128px;
    height: 36px;
    border-radius: 18px;
    display: inline-block;
    text-align: center;
    line-height: 36px;
    color: #fff;
    background: #4c84ff;
    margin-left: 10px;
    cursor: pointer;
  }
}

.commend .commend-list {
  padding: 0 30px 0;
  .title {
    color: #303133;
    padding-top: 28px;
    padding-bottom: 30px;
    border-top: 1px solid #e0e4eb;
    font-size: 16px;
    line-height: 1;
  }
}
.comment-box {
  display: flex;
  padding-bottom: 40px;
  .avatar {
    img {
      width: 48px;
      height: 48px;
      border-radius: 50%;
    }
  }
  .info {
    flex: 1;
    margin-left: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #E0E4EB;
    .name {margin-bottom: 22px;}
    .time {
      margin-top: 20px;
      color: #909399;
    }
  }
}
// .commend-list > div:last-child {
//   .info {
//     border-bottom: 0;
//   }
// }

.more {
  text-align: center;
  line-height: 68px;
  border-top: 1px solid #E0E4EB;
  span {
    cursor: pointer;
  }
}
</style>
